import Vue from 'vue'
import Vuex from 'vuex'
import axios from './baseUrl'
import router from '../router/index'
import createPersistedState from 'vuex-persistedstate'

const ApTkn = 'fn9l7qiI0n6Wdk9PyWELOH2baMKy7nG64XjDXvcLArYM29kQss70fAXBlw9npqjS'


Vue.use(Vuex)

export default new Vuex.Store({
   state:{
      // State on Home Page
      FirstData: null,
      LabelMovie: null,

      // State on Loader
      LoaderDt: false,

      // State on Header
      ValSearch:null,

      //State for Movie Selected
      PlayerStat:false,
      PlayerData:null

   },
   mutations: {
      // Mutations on Home Page
      MutFirstData(state, FirstData){
         state.FirstData = FirstData
      },
      MutLabelMovie(state, LabelMovie){
         state.LabelMovie = LabelMovie
      },

      // Mutations on Loader
      MutLoaderDt(state, LoaderDt){
         state.LoaderDt = LoaderDt
      },

      // Mutations on ValSearch
      MutValSearch(state, ValSearch){
         state.ValSearch = ValSearch
      },

      // Muttations for Movie Selected
      MutPlayerStat(state, PlayerStat){
         state.PlayerStat = PlayerStat
      },
      MutPlayerData(state, PlayerData){
         state.PlayerData = PlayerData
      }
   },
   actions : {
      
      // Handle Loader Status To False
      SET_LOADER_TO_FALSE({commit}){
         setTimeout(() => {
            commit('MutLoaderDt', false)
         }, 2100);
      },

      SET_STATE_ON_PLAYER_TO_DEFAULT({commit}){
         commit('MutPlayerData', null)
         commit('MutPlayerStat', false)
      },


      // Actions on Home Page
      GET_FIRSTDATA({commit, dispatch}, varPage){
         commit('MutLabelMovie', "Newest Movies")
         dispatch('SET_STATE_ON_PLAYER_TO_DEFAULT')
         
         axios({
            method: 'GET',
            url:`v1/movie/newest?limit=25&page=${varPage}`,
         })
         .then(res => {
            if(res.status === 200 && res.data !== null){
               commit('MutFirstData', res)
               dispatch('SET_LOADER_TO_FALSE')
            }else{
               commit('MutFirstData', "NFD")
               dispatch('SET_LOADER_TO_FALSE')
            }
            
         })
         .catch(err => {
            console.log(err)
            commit('MutFirstData', "NFD")
            dispatch('SET_LOADER_TO_FALSE')
         })
      },
      GET_SEARCH_MOVIE({commit, dispatch}, NmMovie){
         commit('MutLabelMovie', "Search Result")
         dispatch('SET_STATE_ON_PLAYER_TO_DEFAULT')
         
         axios({
            method: 'GET',
            url:`v1/movie/search?title=${NmMovie}&limit=200`,
         })
         .then(res => {
            if(res.status === 200 & res.data !== null){
               commit('MutFirstData', res)
               dispatch('SET_LOADER_TO_FALSE')
            }else{
               commit('MutFirstData', "NFM")
               dispatch('SET_LOADER_TO_FALSE')
            }

         })
         .catch(err => {
            console.log(err)
            commit('MutFirstData', "NFM")
            dispatch('SET_LOADER_TO_FALSE')
         })
      },
      GET_PLAYER_DATA({commit, dispatch}, IdMovie){
         commit('MutLabelMovie', "Stream Movie")
         axios({
            method: 'GET',
            url:`https://api.gdriveplayer.us/v1/imdb/${IdMovie}`,
            headers: {'Access-Control-Allow-Origin': '*'},
            proxy: {
               host: 'robertsoerbakti.github.io',
               port: 8080
            }
         })
         .then(res => {
            if(res.status === 200 & res.data !== null){
               commit('MutPlayerData', res)
               dispatch('SET_LOADER_TO_FALSE')
            }else{
               commit('MutPlayerData', "NTW")
               dispatch('SET_LOADER_TO_FALSE')
            }

         })
         .catch(err => {
            console.log(err)
            commit('MutFirstData', "NTW")
            dispatch('SET_LOADER_TO_FALSE')
         })
      }
      
   },
   getters : {

   },
   plugins: [createPersistedState()],
})


